<template>
  <div id="register" class="wrapper">
    <div class="container">
      <h3 class="title is-3 has-text-centered">Đăng kí</h3>
      <form @submit.prevent="registerUser">
        <section>
          <div class="columns">
            <div class="column"></div>
            <div class="column">
              <div class="card">
                <div class="card-content">
                  <b-field label="Email">
                    <b-input type="email" placeholder="Nhập email của bạn" v-model="email"></b-input>
                  </b-field>

                  <b-field label="Mật khẩu">
                    <b-input type="password" placeholder="Nhập mật khẩu của bạn" v-model="password"></b-input>
                  </b-field>
                  <b-field>
                    <!-- <b-button
                      type="is-primary"
                      v-on:click="registerUser"
                      :disabled="isDisabled"
                    >Đăng ký</b-button>-->
                    <button type="submit" :disabled="isDisabled" class="button is-primary">Đăng ký</button>
                  </b-field>
                </div>
              </div>
            </div>
            <div class="column"></div>
          </div>
        </section>
      </form>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  name: "register",
  data() {
    return {
      email: null,
      password: null
    };
  },
  computed: {
    isDisabled() {
      return !this.email || !this.password;
    }
  },
  methods: {
    registerUser: function(e) {
      console.log("regisster");
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            this.$buefy.dialog.alert(
              `Tạo tk thành công với email ${this.email}`
            );
            // this.$router.push("/");
            this.$router.go({
              path: this.$router.path
            });
          },
          err => {
            console.log(err);
            if ((err.code = "auth/email-already-in-use")) {
              this.$buefy.dialog.alert(
                "Email đã có người sử dụng. Bạn có thể đăng nhập hoặc dùng email khác"
              );
            }
            // alert(err.message);
          }
        );

      e.preventDefault();
    }
  }
};
</script>



