import Vue from "vue";
import Router from "vue-router";
import Dashboard from "@/components/Dashboard";
import NewStation from "@/components/NewStation";
import ViewStation from "@/components/ViewStation";
import EditStation from "@/components/EditStation";
import Login from "@/components/Login";
import Register from "@/components/Register";
import firebase from "firebase";

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      name: "Dashboard",
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/new",
      name: "new-station",
      component: NewStation,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/view/:station_id",
      name: "view-station",
      component: ViewStation,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/edit/:station_id",
      name: "edit-station",
      component: EditStation,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/login",
      name: "login",
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: "/register",
      name: "register",
      component: Register,
      meta: {
        requiresGuest: true
      }
    }
  ]
});

router.beforeEach((to, from, next) => {
  // check for requireAuth
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // check not login
    if (!firebase.auth().currentUser) {
      // go to login
      next({
        path: "/login",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // process to route
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    if (firebase.auth().currentUser) {
      // go to login
      next({
        path: "/",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // process to route
      next();
    }
  } else {
    // process to route
    next();
  }
});

export default router;



