<template>
  <div id="edit-station" class="wrapper">
    <div class="container">
      <h3 class="title is-3">Sửa trạm</h3>
      <section>
        <b-field label="Mã trạm">
          <b-input v-model="station_id" required disabled></b-input>
        </b-field>

        <b-field label="Tên trạm">
          <b-input v-model="station_name" required></b-input>
        </b-field>

        <b-field label="Tọa độ trạm">
          <b-input v-model="station_geo" required></b-input>
        </b-field>

        <b-field label="Địa chỉ trạm">
          <b-input v-model="station_addr" required></b-input>
        </b-field>

        <b-field label="Số điện thoại của trạm">
          <b-input v-model="station_phone" required></b-input>
        </b-field>
        <b-field>
          <b-button type="is-primary" @click="updateStation" :disabled="isDisabled">Cập nhật</b-button>
          <router-link to="/" class="back-btn2">
            <b-button type="is-dark">Quay lại</b-button>
          </router-link>
        </b-field>
      </section>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "edit-station",
  data() {
    return {
      station_id: null,
      station_name: null,
      station_geo: null,
      station_addr: null,
      station_phone: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("stations")
      .where("station_id", "==", to.params.station_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.station_id = doc.data().station_id;
            vm.station_name = doc.data().station_name;
            vm.station_geo = doc.data().station_geo;
            vm.station_addr = doc.data().station_addr;
            vm.station_phone = doc.data().station_phone;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  computed: {
    isDisabled() {
      return (
        !this.station_id ||
        !this.station_name ||
        !this.station_geo ||
        !this.station_addr ||
        !this.station_phone
      );
    }
  },
  methods: {
    fetchData() {
      console.log("fetchData ");
      console.log(this.$route);
      db.collection("stations")
        .where("station_id", "==", this.$route.params.station_id)
        .get()
        .then(querySnapshot => {
          console.log("queerr2 ");
          console.log(querySnapshot);
          querySnapshot.forEach(doc => {
            this.station_id = doc.data().station_id;
            this.station_name = doc.data().station_name;
            this.station_geo = doc.data().station_geo;
            this.station_addr = doc.data().station_addr;
            this.station_phone = doc.data().station_phone;
          });
        });
    },
    updateStation() {
      db.collection("stations")
        .where("station_id", "==", this.$route.params.station_id)
        .get()
        .then(querySnapshot => {
          console.log("queerr2 ");
          console.log(querySnapshot);
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                station_id: this.station_id,
                station_name: this.station_name,
                station_geo: this.station_geo,
                station_addr: this.station_addr,
                station_phone: this.station_phone
              })
              .then(() => {
                this.$buefy.dialog.alert("Cập nhật thành công");
                this.$router.push({
                  name: "view-station",
                  params: {
                    station_id: this.station_id
                  }
                });
              });
          });
        });
    }
  }
};
</script>



