<template>
  <div id="new-station" class="wrapper">
    <div class="container">
      <h3 class="title is-3">Tạo trạm mới</h3>
      <section>
        <b-field label="Mã trạm">
          <b-input v-model="station_id" required></b-input>
        </b-field>

        <b-field label="Tên trạm">
          <b-input v-model="station_name" required></b-input>
        </b-field>

        <b-field label="Tọa độ trạm">
          <b-input v-model="station_geo" required></b-input>
        </b-field>

        <b-field label="Địa chỉ trạm">
          <b-input v-model="station_addr" required></b-input>
        </b-field>

        <b-field label="Số điện thoại của trạm">
          <b-input v-model="station_phone" required></b-input>
        </b-field>
        <b-field>
          <b-button
            type="is-primary"
            @click="saveStation"
            :disabled="isDisabled"
            >Thêm mới</b-button
          >
          <router-link to="/" class="back-btn2">
            <b-button type="is-dark">Quay lại</b-button>
          </router-link>
        </b-field>
      </section>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";

export default {
  name: "new-station",
  data() {
    return {
      station_id: null,
      station_name: null,
      station_geo: null,
      station_addr: null,
      station_phone: null
    };
  },
  computed: {
    isDisabled() {
      return (
        !this.station_id ||
        !this.station_name ||
        !this.station_geo ||
        !this.station_addr ||
        !this.station_phone
      );
    }
  },
  methods: {
    saveStation() {
      console.log("new ssss");
      db.collection("stations")
        .add({
          station_id: this.station_id,
          station_name: this.station_name,
          station_geo: this.station_geo,
          station_addr: this.station_addr,
          station_phone: this.station_phone
        })
        .then(docRef => {
          this.$router.push("/");
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>



