<template>
  <div id="dashboard" class="wrapper">
    <div class="container">
      <h3 class="title is-3">Dashboard</h3>
      <div class="table__wrapper">
        <table class="table is-fullwidth">
          <thead>
            <tr>
              <th>ID</th>
              <th>Tên</th>
              <th>Địa chỉ</th>
              <th>Phone</th>
              <th>Tọa độ</th>
              <th></th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="station in stations" v-bind:key="station.id">
              <td>{{ station.station_id }}</td>
              <td>{{ station.station_name }}</td>
              <td>{{ station.station_addr }}</td>
              <td>{{ station.station_phone }}</td>
              <td>{{ station.station_geo }}</td>
              <td>
                <router-link
                  :to="{
                  name: 'view-station',
                  params: { station_id: station.station_id }
                }"
                >
                  <b-button type="is-info" icon-right="view">Xem chi tiết</b-button>
                </router-link>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      stations: []
    };
  },
  created() {
    console.log("create xcxc");
    db.collection("stations")
      .orderBy("station_addr")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            station_id: doc.data().station_id,
            station_name: doc.data().station_name,
            station_addr: doc.data().station_addr,
            station_phone: doc.data().station_phone,
            station_geo: doc.data().station_geo
          };
          //   console.log(data);
          this.stations.push(data);
        });
      });
  }
};
</script>



