<template>
  <div id="login" class="wrapper">
    <div class="container">
      <h3 class="title is-3 has-text-centered">Đăng nhập</h3>

      <form @submit.prevent="login">
        <section>
          <div class="columns">
            <div class="column"></div>
            <div class="column">
              <div class="card">
                <div class="card-content">
                  <b-field label="Email">
                    <b-input type="email" placeholder="Nhập email của bạn" v-model="email"></b-input>
                  </b-field>

                  <b-field label="Mật khẩu">
                    <b-input type="password" placeholder="Nhập mật khẩu của bạn" v-model="password"></b-input>
                  </b-field>
                  <b-field>
                    <button type="submit" :disabled="isDisabled" class="button is-primary">Đăng nhập</button>
                    <!-- <b-button type="is-primary" v-on:click="login" :disabled="isDisabled">Đăng nhập</b-button> -->
                  </b-field>
                </div>
              </div>
            </div>
            <div class="column"></div>
          </div>
        </section>
      </form>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  name: "login",
  data() {
    return {
      email: null,
      password: null
    };
  },
  computed: {
    isDisabled() {
      return !this.email || !this.password;
    }
  },
  methods: {
    login: function(e) {
      console.log("Login");
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            this.$buefy.dialog.alert(`Bạn đăng nhập với email: ${this.email}`);
            this.$router.push("/");
            this.$router.go({
              path: this.$router.path
            });
          },
          err => {
            console.log(err);
            if (err.code == "auth/user-not-found") {
              this.$buefy.dialog.alert(
                "Tài khoản không tồn tại trong hệ thống"
              );
            } else if (err.code == "auth/wrong-password") {
              this.$buefy.dialog.alert(
                "Email hoặc mật khẩu của bạn không đúng"
              );
            } else {
              this.$buefy.dialog.alert("Có lỗi xảy ra");
            }
            // alert(err.message);
          }
        );

      e.preventDefault();
    }
  }
};
</script>



