<template>
  <div id="view-station" class="wrapper">
    <div class="container">
      <h3 class="title is-3">Chi tiết trạm</h3>
      <div>
        Tên trạm :
        <b>{{ station_name }}</b>
      </div>
      <div>
        Mã trạm :
        <b>{{ station_id }}</b>
      </div>
      <div>
        Địa chỉ :
        <b>{{ station_addr }}</b>
      </div>
      <div>
        Tọa độ :
        <b>{{ station_geo }}</b>
      </div>
      <div>
        Số điện thoại liên lạc :
        <b>{{ station_phone }}</b>
      </div>
      <div class="buttons view-btns">
        <router-link to="/" class="back-btn">
          <b-button type="is-dark">Quay lại</b-button>
        </router-link>
        <router-link
          class="back-btn"
          :to="{
            name: 'edit-station',
            params: { station_id: station_id }
          }"
        >
          <b-button type="is-link">Sửa</b-button>
        </router-link>

        <b-button type="is-danger" @click="deleteStation">Xóa</b-button>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "view-station",
  data() {
    return {
      station_id: null,
      station_name: null,
      station_geo: null,
      station_addr: null,
      station_phone: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("stations")
      .where("station_id", "==", to.params.station_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.station_id = doc.data().station_id;
            vm.station_name = doc.data().station_name;
            vm.station_geo = doc.data().station_geo;
            vm.station_addr = doc.data().station_addr;
            vm.station_phone = doc.data().station_phone;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      console.log("fetchData ");
      console.log(this.$route);
      db.collection("stations")
        .where("station_id", "==", this.$route.params.station_id)
        .get()
        .then(querySnapshot => {
          console.log("queerr2 ");
          console.log(querySnapshot);
          querySnapshot.forEach(doc => {
            this.station_id = doc.data().station_id;
            this.station_name = doc.data().station_name;
            this.station_geo = doc.data().station_geo;
            this.station_addr = doc.data().station_addr;
            this.station_phone = doc.data().station_phone;
          });
        });
    },
    deleteStation() {
      this.$buefy.dialog.confirm({
        message: "Bạn chắc chắn xóa chứ?",
        onConfirm: () => {
          db.collection("stations")
            .where("station_id", "==", this.$route.params.station_id)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                doc.ref.delete();
                this.$router.push("/");
              });
            });
          this.$buefy.toast.open("Đã xóa");
        }
      });
      // if (confirm("Bạn có chắc là muốn xóa trạm này không ?")) {
      //   db.collection("stations")
      //     .where("station_id", "==", this.$route.params.station_id)
      //     .get()
      //     .then(querySnapshot => {
      //       querySnapshot.forEach(doc => {
      //         doc.ref.delete();
      //         this.$router.push("/");
      //       });
      //     });
      // }
    }
  }
};
</script>



