<template>
  <div class="wrap-nav">
    <div class="container">
      <b-navbar shadow="{true}">
        <template slot="brand">
          <b-navbar-item tag="router-link" :to="{ path: '/' }">
            <img src="https://vinfast.vn/sites/default/files/favicon.png" alt="Vinfast" />
          </b-navbar-item>
        </template>
        <template slot="start">
          <b-navbar-item href="#" tag="router-link" :to="{ path: '/' }">Trang chủ</b-navbar-item>

          <b-navbar-item tag="router-link" :to="{ path: '/new' }">Thêm trạm mới</b-navbar-item>
        </template>

        <template slot="end">
          <b-navbar-item tag="div">
            <div class="buttons">
              <!-- <a class="button is-primary">
              <strong>Sign up</strong>
              </a>-->
              <router-link to="/register" class="button is-primary" v-if="!isLogin">
                <strong>Đăng kí</strong>
              </router-link>
              <router-link to="/login" class="button is-light btn-login" v-if="!isLogin">
                <strong>Đăng nhập</strong>
              </router-link>
              <a class="button is-primary" v-on:click="logout" v-if="isLogin">Đăng xuất</a>
            </div>
          </b-navbar-item>
        </template>
      </b-navbar>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  name: "navbar",
  data() {
    return {
      isLogin: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLogin = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      console.log("logout");
      firebase
        .auth()
        .signOut()
        .then(() => {
          // this.$router.push("/login");
          this.$router.go({
            path: this.$router.path
          });
        });
    }
  }
};
</script>
<style scoped>
.wrap-nav {
  background: #222;
}
.btn-login {
  background: red !important;
}
</style>



